<template>
    <v-layout fluid>
        <v-card width="100%">
            <v-card-title class="justify-center">Declaración de Elegibilidad</v-card-title>
            <v-card-text>
               <v-row>
                  <v-col cols="12">
                    <span class="h2 font-weight-bold black--text">{{ `Estado: ${estadotxt}` }}</span>
                  </v-col>
                </v-row>
                <br>
                <v-window>
                  <v-window-item
                    key="card-1"
                  >
                    <v-card
                     color="white"
                     elevation="2"
                    >
                     <v-row
                        class="fill-height"
                        align="center"
                        justify="center"
                      >
                        <v-col cols="12">
                            Chachara, chachara, chara
                        </v-col>
                      </v-row>
                    </v-card>
                  </v-window-item>
                </v-window>
                <v-row>
                  <v-col cols="12">
                    <v-checkbox
                      color="primary"
                      v-model="aceptaterminosact"
                      label="Aceptar Términos y Condiciones"
                      :disabled="aceptaterminosant"
                    ></v-checkbox>
                  </v-col>
                </v-row>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text @click="actualizaTerminos" :disabled="aceptaterminosant || !aceptaterminosact">Actualizar</v-btn>
            </v-card-actions>
        </v-card>
    </v-layout>
</template>

<script>
import { registroProponentes } from '@/services/api'

export default {
  name: 'regDeclaracion',
  props: ['email', 'codigo', 'dataRegistro', 'aceptaterminos', 'estadotxt'],
  data: () => ({
    aceptaterminosact: false,
    aceptaterminosant: false
  }),
  methods: {
    async actualizaTerminos () {
      try {
        const aceptaterminosTF = this.aceptaterminosact === true ? 1 : 0
        const { data } = await registroProponentes.actualizaTCRegistro({ codigo: this.codigo, aceptaterminos: aceptaterminosTF })
        if (data) {
          this.$toasted.success('Se actualizó los términos y condiciones', { icon: 'done', duration: 5000 })
          this.$store.commit('updateRegistro', true)
        } else {
          this.$toasted.error('Se presentó un error al tratar de actualizar', { icon: 'error', duration: 5000 })
        }
      } catch (error) {
      } finally {
      }
    }
  },
  watch: {
    dataRegistro: function (val) {
      const aceptaterminsTF = this.aceptaterminos === 1
      this.aceptaterminosact = aceptaterminsTF
      this.aceptaterminosant = aceptaterminsTF
    },
    aceptaterminos: function (val) {
      const aceptaterminsTF = val === 1
      this.aceptaterminosact = aceptaterminsTF
      this.aceptaterminosant = aceptaterminsTF
    }
  },
  mounted () {
    const aceptaterminsTF = this.aceptaterminos === 1
    this.aceptaterminosact = aceptaterminsTF
    this.aceptaterminosant = aceptaterminsTF
  }
}
</script>
